import React, { useEffect, useState } from "react";
import "../componentStyles.css";
import flags from "../flags.js";

const Card = (props) => {
  const handleClick = (e) => {
    if (!props.hasBeenClicked) {
      props.setCurrentScore(props.currentScore + 1);
      let oldFlagState = props.allFlags.map((flag) => {
        if (flag.country === props.country) {
          return {
            country: `${flag.country}`,
            src: `${flag.src}`,
            hasBeenClicked: true,
          };
        } else {
          return flag;
        }
      });
      props.setAllFlags(oldFlagState);
    } else {
      props.setCurrentScore(0);
      props.setAllFlags(flags);
    }
  };

  return (
    <div className="flag-container">
      <img
        className="flag-image"
        onClick={handleClick}
        src={require(`./${props.src}`)}
        alt={props.country}
      ></img>
      <div className="country-code">{props.country}</div>
    </div>
  );
};

export default Card;
