import React, { useEffect, useState } from "react";
import "../componentStyles.css";
import flags from "../flags.js";
import Score from "./Score.js";
import CardGrid from "./CardGrid.js";

const Board = () => {
  let [currentScore, setCurrentScore] = useState(0);
  let [hiscore, setHiscore] = useState(0);

  let [allFlags, setAllFlags] = useState(flags);

  const randomFlagsGenerator = () => {
    let flagArray = [];
    for (let i = 0; i < 9; i++) {
      let random = Math.floor(Math.random() * 20);
      while (flagArray.includes(random)) {
        random = Math.floor(Math.random() * 20);
      }
      flagArray.push(random);
    }
    flagArray = flagArray.map((number) => {
      return allFlags[number];
    });
    return flagArray;
  };

  return (
    <div className="game-board">
      <Score
        currentScore={currentScore}
        hiscore={hiscore}
        setHiscore={setHiscore}
      />
      <CardGrid
        randomNineFlags={randomFlagsGenerator()}
        allFlags={allFlags}
        setAllFlags={setAllFlags}
        currentScore={currentScore}
        setCurrentScore={setCurrentScore}
        hiscore={hiscore}
        setHiscore={setHiscore}
      />
    </div>
  );
};

export default Board;
